.login-main {
    left: 0;
    top: 0;
    height: 100%;
    position: fixed;
    background: #E7E9EF 0% 0% no-repeat padding-box;
    font-family: 'Usual', sans-serif;
    opacity: 1;
    font-size: 14px;
    color: #151C1B;

    .login-shape-1 {
        left: 0;
        top: 0;
        width: 45%;
        height: 110%;
        position: fixed;
    }

    .login-shape-2 {
        top: 0;
        right: 0;
        position: fixed;
    }
    
    .login-block {
        padding: 0 10%;

        .login-logo {
            padding-top: 28px;
            width: 74px;
            // height: 34px;
        }

        .login-project-name {
            padding-top: 4rem;
            padding-bottom: 4rem;
            font-weight: 600;
            width: 386px;
            height: 44px;

            text-align: left;
            font-size: 32px;
            letter-spacing: 0;
            color: #272E4C;
            opacity: 1;
        }

        .login-button {
            margin-top: 56px;
            width: 100%; 
            height: 48px;
            background: #3AB77D 0% 0% no-repeat padding-box;
            border-radius: 8px;
            opacity: 1;
            color: #FFFFFF;
        }

        
    }
}

.login-input-area {
    padding-top: 28px;

    .login-text {
        font-size: 14px;
    }

    .login-text-field {
        width: 100%;
        padding: 10px 16px;
        font-size: 16px;
        background: #FFFFFF 0% 0% no-repeat padding-box;
        border: 0.5px solid #272e4c4d;
        border-radius: 8px;
        font-family: 'Usual';
        margin-top: 10px;
        // &.input {
        //     padding: 0;
        //     font-family: 'Usual';
        // }
        &.MuiInputBase-input {
            font-family: 'Usual', serif;
        }
    }
    &.MuiInputBase-input {
        font-family: 'Usual', serif;
    }
}